let sex=['不限','男','女'];
let qg=['秘密','未婚','已婚'];
let job=['秘密','IT','老师'];
export default {
	data() {
		return {
			userpic:"../../static/demo/userpic/11.jpg",
			username:"哈哈哈",
			sex:"不限",
			qg:"未婚",
			job:"IT",
			birthday:"1987-02-07"
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		// 修改生日
		bindDateChange(e) {
			this.birthday = e.target.value
		},
		// 修改头像
		changeimg(){
			uni.chooseImage({
				count:1,
				sizeType:['compressed'],
				success: (res) => {
					this.userpic=res.tempFilePaths;
				}
			})
		},
		// 单列选择
		changeOne(val){
			let arr=[];
			switch (val){
				case 'sex':
				arr=sex;
					break;
				case 'qg':
				arr=qg;
					break;
				case 'job':
				arr=job;
					break;
			}
			uni.showActionSheet({
				itemList: arr,
				success: res => {
					switch (val){
						case 'sex':
						this.sex=arr[res.tapIndex];
							break;
						case 'qg':
						this.qg=arr[res.tapIndex];
							break;
						case 'job':
						this.job=arr[res.tapIndex];
							break;
					}
				},
			});
		},
		submit(){
			
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
}
